.actionContainer {
  margin-top: 0px;
}

.actionBanner {
  max-width: 100%;
  padding: 0px;
  margin: auto;
  height: 300px;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.actionBannerContainer {
  top: 60px;
  margin: auto;
  position: relative;
  text-align: center;
}

.actionBannerText {
  font-size: 300%;
  color: floralwhite;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -80%);
      -ms-transform: translate(-50%, -80%);
          transform: translate(-50%, -80%);
  border: 1px solid floralwhite;
  padding: 20px;
}

.cardsContainer {
  margin-top: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.card {
  margin: 20px;
  width: 200px;
}

.card:hover {
  -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
          box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.cardImg {
  width: 200px;
  height: 175px;
  -o-object-fit: cover;
     object-fit: cover;
}

.cardText {
  padding: 0px 10px 0px 10px; 
}


